#wnpApp {
  background: linear-gradient(0deg, #010c12 0%, #033536 50%, #28434a 100%);
  width: 100vw;
  max-width: 100%;
  height: 100vh;
  overflow: hidden;
}

#wnpApp .text-secondary {
  color: #b3bdba !important;
}

#wnpApp .wnpContainer {
  color: #fff;
  grid-template: "wnpAlbumArt wnpMediaInfo" 1fr
                 "wnpFooter wnpFooter" 110px
                 / 1fr 1fr;
  grid-auto-flow: row;
  gap: 0;
  width: 100%;
  height: 100%;
  display: grid;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

#wnpApp .wnpAlbumArt {
  text-align: center;
  grid-area: wnpAlbumArt;
  padding: 30px;
  overflow: hidden;
}

#wnpApp .wnpMediaInfo {
  grid-area: wnpMediaInfo;
  padding: 30px 30px 30px 0;
}

#wnpApp .wnpFooter {
  grid-area: wnpFooter;
  padding: 0 30px;
}

@media screen and (width <= 500px) {
  #wnpApp .wnpContainer {
    grid-template: "wnpAlbumArt" 2fr
                   "wnpMediaInfo" 1fr
                   "wnpFooter" 140px
                   / 1fr;
    gap: 0;
    display: grid;
  }

  #wnpApp .wnpMediaInfo {
    padding: 0 30px;
  }
}

#wnpApp #wnpBgAlbumArt {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

#wnpApp #wnpBgAlbumArt #bgAlbumArtBlur {
  filter: blur(12px) brightness(.6) opacity(.6);
  background-image: url("fake-album-1.2ade3ad0.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
  transform: scale(1.25);
}

#wnpApp .wnpAlbumArt #albumArt {
  object-fit: contain;
  max-width: 100%;
  max-height: 100%;
  box-shadow: 3px 3px 16px #000000bf;
}

#wnpApp .wnpMediaInfo {
  text-shadow: 3px 3px 8px #000000bf;
}

#wnpApp .wnpMediaInfo #mediaSource {
  text-align: center;
}

#wnpApp .wnpMediaInfo #mediaSource img {
  filter: invert();
  max-height: 16px;
  margin-bottom: 15px;
}

#wnpApp .wnpMediaInfo #mediaQualityIdent.badge-outlined {
  font-weight: var(--bs-body-font-weight);
  border: 1px solid #b3bdba;
  margin-right: 10px;
}

#wnpApp .wnpFooter #progressPercent {
  --bs-bg-opacity: .5;
}

#wnpApp .wnpFooter .btn-group {
  margin-top: 5px;
}

#wnpApp .wnpFooter .btn-group button {
  border: none;
}

#wnpApp .wnpFooter .btn-group button:hover, #wnpApp .wnpFooter .btn-group button:active {
  color: var(--bs-btn-color);
  background-color: #0000;
}

#wnpApp .wnpFooter #btnPlay {
  font-size: 2.25rem;
}

#wnpApp .wnpFooter #btnPrev, #wnpApp .wnpFooter #btnNext {
  font-size: 1.75rem;
}

#wnpApp .wnpFooter #devNameHolder {
  position: absolute;
  bottom: 15px;
  left: 30px;
}

#wnpApp .wnpFooter #devVolumeHolder {
  text-align: right;
  position: absolute;
  bottom: 15px;
  right: 30px;
}

#wnpApp .wnpFooter #wnpSettings {
  border: none;
  position: absolute;
  top: 5px;
  right: 15px;
}
